<template>
  <div>
    <el-row type="flex" :gutter="20">
      <el-col :span="17">
        <el-card>
          <div slot="header" class="clearfix">
            <span>{{ this.partInfo.title }}</span>
          </div>
          <ckeditor
            :editor="editor"
            v-model="editorData"
            :config="editorConfig"
          ></ckeditor>
        </el-card>
      </el-col>

      <el-col :span="7">
        <el-card style="margin-bottom: 20px;">
          <el-form
            label-position="top"
            label-width="80px"
            :model="partInfo"
            status-icon
            :rules="partInfoRules"
          >
            <el-form-item class="form-item" label="" prop="title">
              <el-input
                v-model="partInfo.title"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-button type="primary" style="width: 100%;" @click="updatePart"
              >/</el-button
            >
          </el-form>
        </el-card>

        <el-card>
          <el-upload
            accept=".mp4"
            :action="$serverBaseUrl + '/api/uploadCourseVideoChunks'"
            :data="{
              courseID: $route.query.id,
              chapter: $route.query.c,
              part: $route.query.p
            }"
            ref="upload"
            :http-request="uploadVideo"
            :with-credentials="true"
            :file-list="videoes"
            :on-remove="delVideo"
            :on-preview="checkVideoUrl"
          >
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">
              <p></p>
              <p>1</p>
              <p>2mp4</p>
            </div>
          </el-upload>
        </el-card>

        <el-dialog
          title=""
          :visible.sync="uploadVideoScuessDialogVis"
          width="30%"
          :before-close="() => false"
          :show-close="false"
        >
          <p></p>
          <el-input
            v-model="uploadVideoScuessUrl[uploadVideoScuessName]"
            id="uploaded-video-url"
          ></el-input>
          <p></p>
          <span slot="footer">
            <el-button plain @click="uploadVideoScuessDialogVis = false"
              > </el-button
            >
            <el-button type="primary" @click="copyUploadVideoScuessUrl"
              ></el-button
            >
          </span>
        </el-dialog>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {CoursesProvider} from '@/provider/index';

import ClassicEditor from '@ckeditor/ckeditor5-editor-classic/src/classiceditor';

import EssentialsPlugin from '@ckeditor/ckeditor5-essentials/src/essentials';
import HeadingPlugin from '@ckeditor/ckeditor5-heading/src/heading';
import BoldPlugin from '@ckeditor/ckeditor5-basic-styles/src/bold';
import ItalicPlugin from '@ckeditor/ckeditor5-basic-styles/src/italic';
import FontPlugin from '@ckeditor/ckeditor5-font/src/font';
import UnderlinePlugin from '@ckeditor/ckeditor5-basic-styles/src/underline';
import AlignmentPlugin from '@ckeditor/ckeditor5-alignment/src/alignment';
import SuperScriptPlugin from '@ckeditor/ckeditor5-basic-styles/src/superscript';
import CodePlugin from '@ckeditor/ckeditor5-basic-styles/src/code';
import LinkPlugin from '@ckeditor/ckeditor5-link/src/link';
import ParagraphPlugin from '@ckeditor/ckeditor5-paragraph/src/paragraph';
import CKFinder from '@ckeditor/ckeditor5-ckfinder/src/ckfinder';
import Image from '@ckeditor/ckeditor5-image/src/image';
import ImageToolbar from '@ckeditor/ckeditor5-image/src/imagetoolbar';
import ImageUpload from '@ckeditor/ckeditor5-image/src/imageupload';
import ImageCaption from '@ckeditor/ckeditor5-image/src/imagecaption';
import ImageStyle from '@ckeditor/ckeditor5-image/src/imagestyle';

import MediaEmbedPlugin from '../../../components/ckeditor5/ckeditor5-media-embed/src/mediaembed';

import uuidV1 from 'uuid/v1';

export default {
  data() {
    return {
      courseID: '',
      chapter: 0,
      part: 0,
      tchID: '',

      editor: ClassicEditor,
      editorData: '',
      editorConfig: {
        plugins: [
          EssentialsPlugin,
          HeadingPlugin,
          BoldPlugin,
          ItalicPlugin,
          FontPlugin,
          UnderlinePlugin,
          AlignmentPlugin,
          SuperScriptPlugin,
          CodePlugin,
          LinkPlugin,
          ParagraphPlugin,
          CKFinder,
          Image,
          ImageToolbar,
          ImageUpload,
          ImageCaption,
          ImageStyle,
          MediaEmbedPlugin
        ],

        toolbar: {
          items: [
            'heading',
            '|',
            'link',
            'imageUpload',
            // 'imageStyle:full',
            // 'imageStyle:side',
            'mediaEmbed',
            '|',
            'bold',
            'italic',
            'fontSize',
            'underline',
            'superscript',
            'code',
            '|',
            'alignment:left',
            'alignment:right',
            'alignment:center',
            'alignment:justify',
            '|',
            'undo',
            'redo'
          ]
        },

        ckfinder: {
          // Upload the images to the server using the CKFinder QuickUpload command.
          uploadUrl: `${this.$serverBaseUrl}/api/uploadCourseImg?courseID=${this.$route.query.id}&chapter=${this.$route.query.c}&part=${this.$route.query.p}`,
          options: {
            resourceType: '.jpg,.jpeg,.png'
          }
        }
      },

      partInfo: {
        title: ''
      },
      partInfoRules: {
        title: [{required: true, message: '', trigger: 'blur'}]
      },

      videoes: [],
      uploadVideoScuessDialogVis: false,
      uploadVideoScuessUrl: {},
      uploadVideoScuessName: ''
    };
  },
  props: {
    user: {
      type: Object,
      default: {}
    }
  },
  created() {
    this.chapter = parseInt(this.$route.query.c);
    this.part = parseInt(this.$route.query.p);
    this.courseID = this.$route.query.id;
    this.tchID = this.user.id;
    this.getPartDetail();
  },
  methods: {
    encodeFilename(filename) {
      return filename
        .replace(/\+/g, '%2B')
        .replace(/ /g, '-')
        .replace(/\//g, '%2F')
        .replace(/\?/g, '%3F')
        .replace(/#/g, '%23')
        .replace(/&/g, '%26')
        .replace(/=/g, '%3D');
    },

    decodeFilename(filename) {
      return filename
        .replace(/%2B/g, '+')
        .replace(/%2F/g, '/')
        .replace(/%3F/g, '?')
        .replace(/%23/g, '#')
        .replace(/%26/g, '&')
        .replace(/%3D/g, '=');
    },
    getPartDetail() {
      CoursesProvider.getPartDetail({
        courseID: this.courseID,
        chapter: this.chapter,
        part: this.part
      })
        .then(res => {
          if (res.data.code === 1) {
            this.partInfo.title = res.data.data.title;
            this.editorData = res.data.data.content || '';
            this.videoes = res.data.data.videoes;
            this.videoes.forEach(video => {
              this.uploadVideoScuessUrl[video.name] = video.url;
            });
          }
          if (res.data.code === -1) {
            console.log(res.data.errMsg);
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            })
              .then(() => {
                this.$router.push({path: '/emptyPage'});
              })
              .catch(() => {});
          }
        })
        .catch(err => {
          console.log(err);
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
            .then(() => {
              this.$router.push({path: '/emptyPage'});
            })
            .catch(() => {});
        });
    },
    updatePart() {
      if (!this.partInfo.title) {
        return null;
      }
      this.$confirm('/, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          CoursesProvider.updatePart({
            courseID: this.courseID,
            chapter: this.chapter,
            part: this.part,
            title: this.partInfo.title,
            content: this.editorData,
            tchID: this.tchID
          })
            .then(res => {
              if (res.data.code === 1) {
                this.$router.push({path: `/course/${this.courseID}/index`});
              }
              if (res.data.code === 0) {
                this.$message({
                  message: res.data.info,
                  type: 'warning'
                });
              }
              if (res.data.code === -1) {
                this.$alert('', '', {
                  confirmButtonText: ''
                });
                console.log(res.data.errMsg);
              }
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(() => {});
    },
    checkVideoUrl(file) {
      this.uploadVideoScuessName = this.encodeFilename(file.name);
      this.uploadVideoScuessDialogVis = true;
    },
    delVideo(file, fileList) {
      // bug
      this.videoes.push({});
      this.videoes.pop();

      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          CoursesProvider.delCourseVideo({
            filename: file.name,
            courseID: this.courseID,
            chapter: this.chapter,
            part: this.part,
            tchID: this.tchID
          })
            .then(res => {
              if (res.data.code === 1) {
                this.$message({
                  type: 'success',
                  message: '!'
                });
                this.videoes = this.videoes.filter(video => {
                  if (video.name === file.name) {
                    return false;
                  }
                  return true;
                });
              }
              if (res.data.code === -1) {
                this.$alert('', '', {
                  confirmButtonText: ''
                });
                console.log(res.data.errMsg);
              }
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(err => {});
    },
    copyUploadVideoScuessUrl() {
      const input = document.getElementById('uploaded-video-url');
      input.select();
      this.uploadVideoScuessDialogVis = false;
      if (document.execCommand('copy')) {
        document.execCommand('copy');
        this.$message({
          type: 'success',
          message: '!'
        });
      } else {
        this.$message({
          type: 'warn',
          message: '!'
        });
      }
    },
    handleUpload(res) {
      if (res.data.code === 1) {
        this.uploadVideoScuessUrl[this.encodeFilename(res.data.data.filename)] =
          res.data.data.url;
        this.uploadVideoScuessName = this.encodeFilename(
          res.data.data.filename
        );
        this.uploadVideoScuessDialogVis = true;
      }
      if (res.data.code === -1) {
        this.$alert('', '', {
          confirmButtonText: ''
        });
      }
    },
    handleUploadError(err) {
      this.$alert('', '', {
        confirmButtonText: ''
      });
      console.log(err);
    },
    uploadVideo(fileObj) {
      const file = fileObj.file;
      const eachSize = 50 * 1024 * 1024; // chunks
      const blockCount = Math.ceil(file.size / eachSize); // 
      const axiosArray = []; // axiosPromise
      const ext = file.name.split('.').slice(-1)[0]; // 
      const hash = uuidV1(); // hash
      const chunksLoads = [];

      // 
      for (let i = 0; i < blockCount; i++) {
        let start = i * eachSize,
          end = Math.min(file.size, start + eachSize);

        const form = new FormData();
        form.append('file', file.slice(start, end));
        form.append('index', i);
        form.append('hash', hash);

        //  Promise 
        axiosArray.push(
          this.$http.request({
            url: 'api/uploadCourseVideoChunks',
            method: 'post',
            data: form,
            onUploadProgress: progressEvent => {
              chunksLoads[i] = progressEvent.loaded;
              const allChunksLoads = chunksLoads.reduce((prev, cur) => {
                return prev + cur;
              });
              const complete = ((allChunksLoads / file.size) * 100) | 0;
              fileObj.onProgress({percent: complete === 100 ? 99 : complete}); // 100%99
            }
          })
        );
      }

      // 
      return this.$http.all(axiosArray).then(() => {
        // chunks
        const data = {
          name: file.name,
          total: blockCount,
          ext,
          hash,
          courseID: this.courseID,
          chapter: this.chapter,
          part: this.part
        };
        // 
        CoursesProvider.mergeCourseVideoChunks(data).then(
          this.handleUpload,
          this.handleUploadError
        );
      });
    }
  }
};
</script>

<style lang="scss">
.ck-editor__editable {
  margin-bottom: 50px;
  min-height: 70vh;
}
</style>
